h2 {
  background-color: black;
  color: white;
  padding: 1.5rem;
  margin-bottom: 1.5rem;
}

.thumbnail {
  width: 100%;
  aspect-ratio: 4/3;
  object-fit: cover;
}

.thumbnail-w {
  width: 20%;
}

@media screen and (max-width: 700px) {
  .thumbnail-w {
    width: 40%;
  }
}

.string-w {
  width: 80%;
}

@media screen and (max-width: 700px) {
  .string-w {
    width: 60%;
  }
}

.thumbnail-date {
  font-size: 0.5rem;
}

@media screen and (max-width: 700px) {
  h3 {
    font-size: 20px;
  }
}

